.main-tabs .nav-link img {
  height: 24px;
  display: block;
  margin: 0 auto 5px auto;
}
.main-tabs.nav-tabs .nav-item .nav-link {
  border: none !important;
  padding: 0.75rem 1.5rem;
  color: #495057;
  font-weight: 500;
  border-radius: 10px;
  margin-right: 15px;
  margin-bottom: 20px;
