.latest-new {
  position: absolute;
  top: -20px;
  left: 50%;
  margin-left: -20px;
}
.latest-new::before {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  bottom: -10px;
  margin-left: -5px;
  left: 50%;
