  border-width: 5px;
}

.footer-alt {
  padding: 25px 0;
  background-color: #152436;
}
.footer-alt .social-icon {
  background-color: #192b41;
  border-radius: 50%;
  color: #6d839f;
  width: 36px;
  height: 36px;
  display: flex;
  font-size: 15px;
  line-height: 32px;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
}
.footer-alt .social-icon:hover {
