.gradient-title {
  background: -webkit-linear-gradient(45deg, #0ab39c, #405189);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section {
  padding-top: 80px;
  padding-bottom: 80px;
}

.section-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 0px;
  padding-bottom: 2.2rem;
  line-height: 1;
  font-size: 1.75rem;
  font-weight: 600;
  position: relative;
}
.section-title::before {
  content: "";
  position: absolute;
  border-bottom: 2px solid #299cdb;
  width: 80px;
  right: 0px;
  left: 0px;
  bottom: 20px;
  margin: 0px auto;
}

.text-shine {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 12px 48px;
  background: linear-gradient(to right, #fff6f6 0, #e75555 10%, #ffffff 20%);
  background-position: 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 3s infinite linear;
  animation-fill-mode: forwards;
  -webkit-text-size-adjust: none;
  text-decoration: none;
  white-space: nowrap;
}

@keyframes shine {
  0% {
    background-position: 0;
  }
  60% {
    background-position: 180px;
  }
  100% {
    background-position: 240px;
  }
}
::placeholder {
  color: #adb1c3 !important;
  font-size: 13px !important;
}

.coming-soon-item .nav-link {
  opacity: 0.5;
  cursor: progress;
}

#back-to-top {
  width: 35px;
  height: 35px;
  position: fixed;
  bottom: 20px;
  left: 30px;
  background: #485b9a;
  border-color: transparent;
  border-radius: 5px;
  color: #fff;
  transition: all 0.5s ease;
  opacity: 0;
  z-index: 999;
}
#back-to-top:hover {
  border-radius: 5px;
  background-color: #0ab39c;
}

.shape {
  position: absolute;
  pointer-events: none;
  right: 0;
  bottom: -2px;
  left: 0;
}
.shape > svg {
  transform: scale(2);
  width: 100%;
  height: auto;
  transform-origin: top center;
}

.btn-blinking {
  animation: blink 1s linear infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
