.ribbon-two {
  position: absolute;
  right: -5px;
  top: -5px;
  z-index: 1;
  overflow: hidden;
  width: 75px;
  height: 75px;
  text-align: right;
}
.ribbon-two span {
  font-size: 13px;
  color: #fff;
  background-color: #f06548;
  text-align: center;
  line-height: 20px;
  transform: rotate(45deg);
  width: 100px;
  display: block;
  position: absolute;
  top: 19px;
  right: -21px;
  font-weight: 600;
}
.ribbon-two span:before {
  content: "";
  position: absolute;
  right: 0;
  top: 100%;
  z-index: -1;
  border-right: 3px solid #da3412;
  border-bottom: 3px solid #da3412;
}
.ribbon-two span:after {
  content: "";
  position: absolute;
  left: 0;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #da3412;
  border-bottom: 3px solid #da3412;
}
.ribbon-two.ribbon-two-primary span {
  background-color: #0ab39c;
}
.ribbon-two.ribbon-two-primary span:before {
  border-right-color: #066b5d;
  border-bottom-color: #066b5d;
}
.ribbon-two.ribbon-two-primary span:after {
  border-left-color: #066b5d;
  border-bottom-color: #066b5d;
}

.bg-hero {
