.demo-box {
  text-align: center;
  position: relative;
  border-radius: 3px;
  padding: 25px 25px 0 25px;
  background-color: #f3f6f9;
  transition: all 0.5s ease;
}
.demo-box .demo-img {
  display: block;
  transition: all 5s ease-in-out;
  border-radius: 5px;
}
.demo-box img {
  width: 100%;
  height: auto;
  position: relative;
  top: 0;
  border-radius: 5px 5px 0 0;
}
.demo-box h4 {
  text-transform: uppercase;
  font-size: 14px;
  margin: 0;
}
.demo-box .demo-img-box {
  position: relative;
  overflow: hidden;
  box-shadow: rgba(100, 100, 111, 0.1) 0px -6px 29px 0px;
}
.demo-box .demo-img-box .demo-btn {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transform: translateY(45px);
  transition: all 0.4s;
}
.demo-box:hover .demo-img-box .demo-btn {
  background-color: rgba(243, 246, 249, 0.5);
  opacity: 1;
  transform: translateY(0px);
}

.bg-cta {
