}

.client-box {
  border: 1px dashed #e9ebec;
}
.client-box p {
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 4;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  white-space: normal;
}

.client-slider .swiper-button-next,
.client-slider .swiper-button-prev {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(4px);
  opacity: 0;
}
.client-slider .swiper-button-next::after,
.client-slider .swiper-button-prev::after {
  font-family: "remixicon";
  font-size: 30px;
}
.client-slider .swiper-button-next::after {
  content: "\ea6e";
}
.client-slider .swiper-button-prev::after {
  content: "\ea64";
}
.client-slider:hover .swiper-button-next,
.client-slider:hover .swiper-button-prev {
  opacity: 1;
}

.homeslider .swiper-slide {
  opacity: 0.1;
}
.homeslider .swiper-slide-active {
  opacity: 1 !important;
}

